import React from "react";
import { Button } from "reactstrap";
import Layout from "../components/Layout";
import { Link } from "next/link";

export default class Tracklist extends React.Component {
  static async getInitialProps({ req, query, params }) {
    if (req) {
      try {
        return { query: req.query, params: req.params };
      } catch (err) {
        return { query: req.query, params: req.params };
      }
    }
    return { query, params };
  }
  constructor(props) {
    super(props);
    this.state = {
      tracklistjunkie: {},
      key: ""
    };
  }
  componentDidMount() {
    const ref = firebase
      .firestore()
      .collection("tracklistjunkie")
      .doc(this.props.params.id);
    ref.get().then(doc => {
      if (doc.exists) {
        this.setState({
          tracklistjunkie: doc.data(),
          key: doc.id,
          isLoading: false
        });
      } else {
        console.log("no data");
      }
    });
  }
  render() {
    return (
      <Layout>
        <div>
          <div className="container">
            <div className="panel panel-default">
              <div className="panel-heading">
                <h4>
                  <Link href="/">Board List</Link>
                </h4>
                <h3 className="panel-title">
                  {this.state.tracklistjunkie.title}
                </h3>
              </div>
              <div className="panel-body">
                <dl>
                  <dt>url:</dt>
                  <dd>{this.state.tracklistjunkie.url}</dd>
                  <dt>Author:</dt>
                  <dd>{this.state.tracklistjunkie.timestamp}</dd>
                </dl>
                <Link
                  href={`/edit/${this.state.key}`}
                  className="btn btn-success"
                >
                  Edit
                </Link>
                &nbsp;
                <Button
                  onClick={this.delete.bind(this, this.state.key)}
                  className="btn btn-danger"
                >
                  Delete
                </Button>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    );
  }
}
